import React from 'react';

const AlertDetails = ({ alert }) => {
  return (
    <div className="alert-box">
      <h3>Code ID: {alert.ID}</h3>
      <p>Last Appeared Location: {alert.location}</p>
      <p>Last Appeared Time: {alert.timestamp}</p>

      {/* Container for side-by-side images */}
      <div className="image-container">
        {/* Display the first image */}
        <img src={alert.image} alt="CCTV" className="image-fixed-size"  />

        {/* Add another image */}
        <img src={alert.criminalDetected} alt="" className="larger-image" />
      </div>
    </div>
  );
};

export default AlertDetails;
